import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import Swal from 'sweetalert2';
import { ComboListService } from '../../../../services/combo.service';

@Component({
  selector: 'app-equipo-form-admin',
  templateUrl: './equipo-form-admin.component.html',
  styleUrls: ['./equipo-form-admin.component.css']
})
export class EquipoFormAdminComponent implements OnInit {

  equipo: any = {};
  edit = false;
  guardar = false;


  createFormGroup() {
    return new FormGroup({
     equipo: new FormControl('', [Validators.required]),
     });
  }



   // tslint:disable-next-line: member-ordering
   Form: FormGroup;


   constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: ComboListService)
    { this.Form = this.createFormGroup(); }

    ngOnInit()
    {

      const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
      if (params.id)
            {
              this.comboListService.getEquipo(params.id) // obtengo el juego con el parametro del id
                .subscribe(
                  res => { // aqui tomo las respuesta
                    console.log(res);
                    this.equipo = res;
                    this.Form.patchValue({
                      equipo: this.equipo.equipo,

                    });

                    this.edit = true;
                  },
                  err => console.log(err)
                );
          }

        }



        GuardarEquipos() {

          // delete this.movie.id; // para excluir los campo cuando se vayan a guardar
          this.comboListService.saveEquipos(this.Form.value).subscribe(
            res => {
                  console.log(res);
                  this.router.navigate(['/equipos/admin/list']);
                  Swal.fire(
                    'Exitoso!',
                    'Datos guardados correctamente...!',
                    'success'
                  );
            },
            err => {
              console.error(err);
              alert('Error!');

            } 
          );
        }


        ActualizarEquipos() {

          this.comboListService.updateEquipos(this.equipo.id, this.equipo)
            .subscribe(
              res => {
                console.log(res);
                this.router.navigate(['/equipos/admin/list']);
                Swal.fire(
                  'Exitoso!',
                  'Datos actualizados correctamente...!',
                  'success'
                );
              },
              err => {
                console.error(err);
                alert('Error!');

              }
            );
        }


        VerificarEquipo() {

          this.comboListService.EquiposRegistradas(this.equipo.equipo).subscribe(
            res => {
              if(Object.keys(res).length >= 1) {
                Swal.fire(
                  'Error!',
                  ` ${this.equipo.equipo}, ya se encuentra registrado. Por favor ingrese otro Equipo...`,
                  'error'
                );
                this.guardar = false;  
                this.onReset();
    
               } 

               else
               {
                Swal.fire(
                  'Exitoso!',
                  `Si puede registrar el equipo: ${this.equipo.equipo} `,
                  'success'
                );
                this.guardar = true;
               }
                 
            },
            err => {
              console.error(err);
              alert('Error!');

            } 
          );
        }



        onReset() {
     
          this.Form.reset();
      }
    


}
